h2 {
    margin-top: 1em;
    padding-top: 1em;
}

h1,
h2 {
    font-weight: 100;
    color: #c0c0c0;
}

a {
    text-decoration: none;
}

span {
    font-weight: 500;
}

.link:hover {
    color: #ff0065;
    -webkit-transition: color 200ms ease-in-out;
    -o-transition: color 200ms ease-in-out;
    transition: color 200ms ease-in-out;
}

.link {
    color: #ffffff;
    -webkit-transition: color 200ms ease-in-out;
    -o-transition: color 200ms ease-in-out;
    transition: color 200ms ease-in-out;
}

.black-white-img {
    -webkit-filter: grayscale(100%); /* Safari 6.0 - 9.0 */
    filter: grayscale(100%);
    -webkit-transition: -webkit-filter 200ms ease-in-out;
    transition: -webkit-filter 200ms ease-in-out;
    -o-transition: filter 200ms ease-in-out;
    transition: filter 200ms ease-in-out;
    transition: filter 200ms ease-in-out, -webkit-filter 200ms ease-in-out;
}

.black-white-img:hover {
    -webkit-filter: grayscale(100%); /* Safari 6.0 - 9.0 */
    filter: grayscale(0%);
    -webkit-transition: -webkit-filter 200ms ease-in-out;
    transition: -webkit-filter 200ms ease-in-out;
    -o-transition: filter 200ms ease-in-out;
    transition: filter 200ms ease-in-out;
    transition: filter 200ms ease-in-out, -webkit-filter 200ms ease-in-out;
}

@media (min-width: 500px) {
    .linkFixedRight {
        position: absolute;
        right: 0;
        max-width: 25%;
    }
}

@media (max-width: 200px) {
    .linkFixedRight {
        position: unset;
        display: -ms-flexbox;
        display: flex;
        max-width: 100%;
    }
}

@media (min-width: 500px) {
    .resumeContainer {
        max-width: 50%;
    }
}

@media (max-width: 500px) {
    .resumeContainer {
        max-width: 100%;
    }
}


@media (min-width: 500px) {
    .justifyResponsive {
        -ms-flex-pack: start;
            justify-content: flex-start;
    }
}

@media (max-width: 500px) {
    .justifyResponsive {
        -ms-flex-pack: center;
            justify-content: center;
    }
}

@media (max-width: 500px) {
    .no-display-mobile {
        display: none !important;
    }
}

@media (min-width: 500px) {
    .only-display-mobile {
        display: none !important;
    }
}


